import empty from '@/components/empty';
import Json from '@/Json';
export default {
	components: {
		empty
	},
	data() {
		return {
			quxiaostate: 0,
			shanchustate: 0,
			tabCurrentIndex: 0,
			showtip: false,
			strtext: '',
			selId: '',
			coupon_code: '',
			coupon_code_show: false,
			//订单状态(0,未付款; 1,已付款; 2,已发货；3,已收货；4,已完成；5,退款中; 6,已退款; 7,已取消; 8,已删除;)
			navList: [{
					state: -1,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				}
			]
		};
	},

	onLoad(options) {

		this.quxiaostate = 0;
		this.shanchustate = 0;
		this.tabCurrentIndex = +options.state;




		if (options.state == 0) {
			this.loadData();
		}
		this.loadData();

	},

	methods: {
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let that = this;
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			console.log('index==' + index);
			console.log('state==' + state);

			// if (source != 'load') {
			// 	if (source === 'tabChange' && navItem.loaded === true) {
			// 		//tab切换只有第一次需要加载数据
			// 		return;
			// 	}
			// 	if (navItem.loadingType === 'loading') {
			// 		//防止重复加载
			// 		return;
			// 	}
			// }



			navItem.loadingType = 'loading';

			this.$Request
				.toget(
					this.$api.order.order_list, {
						status: state == -1 ? '' : '' + state,
					}, {
						authorization: this.$api_token
					}
				)
				.then(res => {
					console.log(res.message);
					if (res.code == 1000) {
						setTimeout(() => {
							let orderList = res.content.filter(item => {
								//添加不同状态下订单的表现形式
								item = Object.assign(item, that.orderStateExp(item.status, item.refund, item.refund_examine));
								//演示数据所以自己进行状态筛选
								return item;
							});
							if (source != 'load') {
								navItem.orderList = [];
								orderList.forEach(item => {
									navItem.orderList.push(item);
								});
							} else {
								navItem.orderList = [];
								orderList.forEach(item => {
									navItem.orderList = item;
								});
							}
							//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
							this.$set(navItem, 'loaded', true);
							//判断是否还有数据， 有改为 more， 没有改为noMore
							navItem.loadingType = 'noMore';
						}, 300);
					} else {
						navItem.orderList = [];
						navItem.loadingType = 'noMore';
					}

				})
				.catch(err => {

				});

		},

		change(e) {
			if (!e.show) {
				this.showtip = false;
				this.coupon_code_show = false;
			}
		},
		cancel(type, state) {
			let that = this;
			// if (state == 2) {
			// if (that.strtext == null || that.strtext == '') {
			// 	uni.showToast({
			// 		title: '请输入退款原因'
			// 	});
			// 	return;
			// }
			uni.request({
				url: that.$api_url + '/refund.api',
				data: {
					openid: uni.getStorageSync('openid'),
					id: that.selId,
					refund_reason: that.strtext
				},
				success: res => {
					uni.showToast({
						title: res.data.message
					});

					that.showtip = false;
					that.strtext = '';

					// uni.redirectTo({
					// 	url: '/pages/order/order?state=4'
					// });
					that.loadData();
				}
			});
			// } else if (state == 1) {
			// 	that.showtip = false;
			// 	that.strtext = '';
			// }
		},
		chsngetext(e) {
			this.strtext = e.detail.value;
			// console.log(e.detail.value)
		},
		toService(item) {
			if (item.status == 0) {
				uni.navigateTo({
					url: '/pages/order/daizhifuOrder?id=' + item.id
				});
			}
			if (item.status == 4) {

				var goodsinfo = 'order/detail';
				var vals = {
					orderId: item.id,
				};
				var xxx = this.$Request.topost(goodsinfo, vals);
				xxx.then(res => {
					// this.$Request.toget(this.$api.goods.goodsinfo, { productId: this.id, type: this.type }, { authorization: uni.getStorageSync('authorization') }).then(res => {
					console.log(res);
					if (res.code == 1000) {
						if (res.content.order != null && res.content.order.lastNum > 0) {
							uni.navigateTo({
								url: '/pages/order/daipinOrder?id=' + item.id
							});
						}
					} else {
						uni.navigateBack();
					}
				});


			}
		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(item) {
			let that = this;
			if(that.shanchustate == 1){
				return
			}
			that.shanchustate = 1;
			uni.showLoading({
				title: '请稍后'
			});

			uni.request({
				url: that.$api_url + '/order/delete', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					orderNo: item.number
				},
				success: res => {
					that.shanchustate = 0;
					uni.showToast({
						title: res.data.message
					});
					// setTimeout(() => {
					// 	that.navList[that.tabCurrentIndex].orderList.splice(item, 1);
						uni.hideLoading();
					// }, 300);
					this.loadData();
					// that.addressList = res.data.data;
				},
				complete:function(e){
					uni.hideLoading();
				}
			});
		},

		//确认收货
		querenshouhuo(item) {
			let that = this;
			uni.showLoading({
				title: '请稍后'
			});

			uni.request({
				url: that.$api_url + '/order/confirm_single', //仅为示例，并非真实接口地址。
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					orderNo: item.id
				},
				success: res => {
					uni.showToast({
						title: res.data.message
					});
					// setTimeout(() => {
					// 	that.navList[that.tabCurrentIndex].orderList.splice(item, 1);
					// 	uni.hideLoading();
					// }, 300);
					that.loadData();
					// that.addressList = res.data.data;
				}
			});
		},
		//取消订单
		cancelOrder(item) {
			let that = this;
			if(that.quxiaostate == 1){
				return
			}
			
			that.quxiaostate = 1;
			uni.showLoading({
				title: '请稍后'
			});

			uni.request({
				url: that.$api_url + '/order/cancel', //仅为示例，并非真实接口地址。
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				method: 'POST',
				data: {
					orderNo: item.number
				},
				success: res => {
					that.quxiaostate = 0;
					setTimeout(() => {
						let {
							stateTip,
							stateTipColor
						} = that.orderStateExp(0, 0, 0);
						item = Object.assign(item, {
							state: 0,
							stateTip,
							stateTipColor
						});

						//取消订单后删除待付款中该项
						// let list = that.navList[that.tabCurrentIndex].orderList;
						// let index = list.findIndex(val => val.id === item.id);
						// console.log('index', index);
						// index !== -1 && list.splice(index, 1);


						this.loadData();
						uni.hideLoading();
					}, 300);
				},
				complete:function(e){
					uni.hideLoading();
				}
			});
		},
		pay(item) {
			let that = this;
			uni.redirectTo({
				url: '/pages/money/pay?id=' + item.id
			});
		},

		ping(item) {
			let that = this;
			uni.redirectTo({
				url: '/pages/order/ping?id=' + item.id
			});
		},
		refund(item) {
			// this.showtip = true;
			let that = this;
			uni.showLoading({
				title: '请稍后'
			});
			that.selId = item.id;
			uni.request({
				url: that.$api_url + '/order/applyRefunding',
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				},
				data: {
					orderId: that.selId
				},
				success: res => {
					uni.showToast({
						title: res.data.message
					});

					that.loadData();
				},
				complete: function(e) {
					uni.hideLoading();
				}
			});
		},

		quanma(item) {
			this.coupon_code = item.coupon_code;
			this.coupon_code_show = true;
		},

		//订单状态文字和颜色
		orderStateExp(state, refund, refund_examine) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 0:
					stateTip = '待付款';
					break;
				case 1:
				case 4:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '已完成';
					break;
				case 6:
					stateTip = '已退款';
					break;
				case 9:
					stateTip = '已驳回';
					break;
				case 5:
					stateTip = '退款中';
					break;
				case 7:
					stateTip = '订单已取消';
					stateTipColor = '#909399';
					break;
				case 8:
					stateTip = '订单已删除';
					stateTipColor = '#909399';
					break;
				case 10:
					stateTip = '已评价';
					break;
					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	}
};
